.div{
    border: 1px solid #ffffff;
    margin: 20px 0px;
    background: #ffffff;
    display: flex;
    justify-content: space-between;
    animation: animate 1s ease-in-out;
}
@keyframes animate {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}
.common{
    border: none;
    outline: none;
    color: #ffffff;
    background: #0F2027;
    font-size: 1.2em;
    padding: 10px 20px;
}
.input{
    text-transform: capitalize;
    width: 50vw;
}
.input:disabled{
    font-weight: 600;
    background: #ffffff;
    color: #0F2027;
}
.button{
    margin: 0px 0px 0px 2px;
}